.navmenu span {
  padding-left: 15px;
  padding-right: 15px;
  
}
.fullscreen{
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.fullscreen main{
  flex-grow: 1;
}
body{
  /* text-align: justify;
  text-justify: inter-word; */
  font-family: 'Poppins', sans-serif;
  overflow-x: hidden;
}

h5{
  text-align: center;
}
/* p{
  text-align: justify;
} */

footer {
  width: 100%;
  background-color: #333;
  color:#fff;
  padding: 10px;
}
.nasscom,.keylinks,.getintouch,.followus{
  width: 100%;
  padding: 10px;
}

.facebookimg, .youtubeimg,.linkedinimg{
  height: 40px;
  margin-right: 13px;
  transition: transform 0.8s;
  cursor: pointer;
}
.facebookimg:hover,.youtubeimg:hover,.linkedinimg:hover{
  transform: rotate(360deg);
}
.keylinks hr{
  margin-top: 3px;
  color: rgb(12, 112, 111);
}
.keylinks a{
  color: rgb(119, 116, 116);
  text-decoration: none;
}
.nasscom p,.getintouch p{
  color: rgb(119, 116, 116);
  margin-left: 20px;
  margin-right: 20px;
}
.keylinks a:hover{
  color: rgb(214, 164, 13);
}
.contactbanner{
  width: 100%;
  height: 450px;
  background-size: contain;
  object-fit: cover;
  background-repeat: no-repeat;
  background-position: center;
}

/*  */
.customcard{
  width: 33%;
  padding: 15px;
  border-radius: 10px;
  box-shadow: #c2bbbb 8px 8px 15px;
  background-color: rgba(243, 239, 228, 0.281);
}

.customcard h3{
  color: rgb(209, 44, 44);
}

.customcard p{
  text-align: start;
}

.customcard2{
  width: 100%;
  padding: 15px;
  border-radius: 10px;
  /* border: rgb(128, 127, 127) 1px solid; */
  /* box-shadow: #c2bbbb 8px 8px 15px; */
  background-color: rgba(243, 239, 228, 0.281);
  background-image: url("./images/wallpapersden.com_line-light-background_2560x1600.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  /* color: aliceblue; */
}
/*  */

@media screen and (max-width:767px){
  .contactbanner{
    width: 100%;
    height: 200px;
  }
  .customcard{
    width: 100%;
  }
}
@media screen and (min-width:768px) and (max-width:991px){
  .contactbanner{
    width: 100%;
    height: 250px;
  }
}